<template>
    <div class="customers">
        <h1 class="title title--primary">Customers</h1>
        <el-table
        :data="customers"
        :row-class-name="'clickable-row'"
        @row-click="rowClicked($event)"
        striped>
            <el-table-column
            prop="fullName"
            label="Name"
            width="225px">
            </el-table-column>
            <el-table-column
            prop="email"
            label="Email address"
            width="225px">
            </el-table-column>
            <el-table-column
            prop="billingAddress"
            label="Billing address"
            width="225px">
            </el-table-column>
        </el-table>
        <el-pagination
            layout="prev, pager, next"
            @current-change="page = $event - 1"
            :hide-on-single-page=true
            :page-count="totalPages">
        </el-pagination>
    </div>
</template>

<script>
    export default {
        name: "customers",
        data() {
            return {
                customersRaw: []
            }
        },
        computed: {
            customers() {
                return this.customersRaw.map((customer) => {
                    const a = customer.billingAddress
                    let firstLine = a.address
                    if(a.flatNumber) { firstLine = a.flatNumber + ", " + firstLine }
                    if(a.company) { firstLine = a.company + ", " + firstLine }
                    customer.billingAddress = firstLine + ", " + a.postcode + ", " + a.city + ", " + a.country.name
                    customer.fullName = a.firstName + " " + a.lastName
                    
                    return customer
                })
            }
        },
        mounted() {
            this.$emit("set-loading")
            this.getCustomers()
        },
        methods: {
            getCustomers() {
                this.$axios.get("/customers")
                .then((res) => {
                    this.customersRaw = res.data.customers
                    this.$emit("stop-loading")
                })
                .catch(() => {
                    this.$message.error("Error loading customers. Please contact your system administrator.")
                })
            }
        }
    }
</script>